import React, { Component } from "react";
import './home.component.css';
import LPFM from '../images/LPFM.jpg';
import MTEH from '../images/MTEH.jpg';
import TFDW from '../images/TFDW.jpg';

export default class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {
      content: "",
    };
  }
  
  componentDidMount() {
        this.setState({
          content: "home page"
        });
        if (localStorage.getItem("userRole")==="ROLE_ADMIN") {
          window.location.reload();
        }
  }

  render() {
    let roleName = "";
        if (localStorage.getItem("userRole")) {
           roleName = localStorage.getItem("userRole");
        } 
    console.log(roleName);
    return (
      <><div id="jumbotron">
        <div id="jumbotron-overlay">
          <div className="row">
            <div id="jumbotron-moto-container">
              <h1>Boks<span>Time</span></h1>
              <h3>The biggest <span>boxing</span> platform!</h3>
              { (!roleName) && (
              <a href="/register">Join Now</a>
              )}
            </div>
          </div>
        </div>
      </div><div id="recent-listings-container" className="section-container">
          <h2>Check out these <span>recent</span> boxing matches...</h2>
          <div className="big-slider-wrapper" id="recent-listings-slider">
            <button className="slider-navigation-button" id="previous-slide">
              <i><ion-icon name="chevron-back-outline"></ion-icon></i>
            </button>
            <div className="slider-container">
            <div className="slider clearfix">
                <a onClick = {()=> {window.location.href =  "/fight?id=3"}} className="item-link">
                  <div
                    className="item"
                    style={{ backgroundImage: `url(${LPFM})` }}
                  >
                    <div className="item-content">
                      <h3>Mayweather vs Paul</h3>
                    </div>
                  </div>
                  <div className="item-desc-container">
                    <p className="item-desc bold">Posted 10 days ago</p>
                    <p className="item-desc">The money fight! The undefeated vs the youtube star!</p>
                  </div>
                </a>
                <a onClick = {()=> {window.location.href =  "/fight?id=2"}} className="item-link">
                  <div
                    className="item"
                    style={{ backgroundImage: `url(${MTEH})` }}
                  >
                    <div className="item-content">
                      <h3>Tyson vs Holyfield</h3>
                    </div>
                  </div>
                  <div className="item-desc-container">
                    <p className="item-desc bold">Posted 3 years ago</p>
                    <p className="item-desc">Biggest fight in the world! Expect the unexpected!</p>
                  </div>
                </a>
                <a onClick = {()=> {window.location.href =  "/fight?id=4"}} className="item-link">
                  <div
                    className="item"
                    style={{ backgroundImage: `url(${TFDW})` }}
                  >
                    <div className="item-content">
                      <h3>Fury vs Wilder 3</h3>
                    </div>
                  </div>
                  <div className="item-desc-container">
                    <p className="item-desc bold">Listed 2 weeks ago</p>
                    <p className="item-desc">Biggest fight of 2021! Two undefeated champions!</p>
                  </div>
                </a>
               
              </div>
            </div>
            <button className="slider-navigation-button" id="next-slide">
              <i><ion-icon name="chevron-forward-outline"></ion-icon></i>
            </button>
          </div>
        </div><div id="categories-container" className="section-container">
          <div className="row">
            <h2>...or explore these <span>fighters</span></h2>
            <div className="trending-categories-wrapper">
              <a href="# " className="item-link left">
                <div className="category-card">
                  <div className="category-card-background-container">
                    <div className="category-card-background"></div>
                  </div>
                  <div className="category-card-text">
                    <p>Test</p>
                    <p>4 listings</p>
                  </div>
                </div>
              </a><a href="# " className="item-link middle">
                <div className="category-card">
                  <div className="category-card-background-container">
                    <div className="category-card-background"></div>
                  </div>
                  <div className="category-card-text">
                    <p>Test</p>
                    <p>10 listings</p>
                  </div>
                </div>
              </a><a href="# " className="item-link right">
                <div className="category-card">
                  <div className="category-card-background-container">
                    <div className="category-card-background"></div>
                  </div>
                  <div className="category-card-text">
                    <p>Test</p>
                    <p>120 listings</p>
                  </div>
                </div>
              </a><a href="# " className="item-link left">
                <div className="category-card">
                  <div className="category-card-background-container">
                    <div className="category-card-background" ></div>
                  </div>
                  <div className="category-card-text">
                    <p>Test</p>
                    <p>200 listings</p>
                  </div>
                </div>
              </a><a href="# " className="item-link middle">
                <div className="category-card">
                  <div className="category-card-background-container">
                    <div className="category-card-background" ></div>
                  </div>
                  <div className="category-card-text">
                    <p>Test</p>
                    <p>0 listings</p>
                  </div>
                </div>
              </a><a href="# " className="item-link right">
                <div className="category-card">
                  <div className="category-card-background-container">
                    <div className="category-card-background"></div>
                  </div>
                  <div className="category-card-text">
                    <p>Test</p>
                    <p>1 listing</p>
                  </div>
                </div>
              </a><a href="# " className="item-link left">
                <div className="category-card">
                  <div className="category-card-background-container">
                    <div className="category-card-background" ></div>
                  </div>
                  <div className="category-card-text">
                    <p>Test</p>
                    <p>100 listings</p>
                  </div>
                </div>
              </a><a href="# " className="item-link middle">
                <div className="category-card">
                  <div className="category-card-background-container">
                    <div className="category-card-background"></div>
                  </div>
                  <div className="category-card-text">
                    <p>Test</p>
                    <p>420 listings</p>
                  </div>
                </div>
              </a><a href="# " className="item-link right">
                <div className="category-card">
                  <div className="category-card-background-container">
                    <div className="category-card-background" ></div>
                  </div>
                  <div className="category-card-text">
                    <p>Test</p>
                    <p>69 listings</p>
                  </div>
                </div>
              </a>
              
            </div>
          </div>
        </div></>
   
    );
  }
}